.PortfolioPage {

}

.portfolios {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  row-gap: 2rem;
  column-gap: 1rem;
  @media screen and (max-width: 1600px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (max-width: 900px) {
    grid-template-columns: repeat(1, 1fr);
  }

  .portfolio {
    .image-data {
      position: relative;
      overflow: hidden;

      &::before {
        content: "";
        position: absolute;
        top: 12px;
        left: 15px;
        height: calc(100% - 30px);
        width: calc(100% - 30px);
        transform-origin: center;
        transform: scale(0);
        transition: all .4s ease-in-out;
      }

      &:hover::before {
        transform: scale(1);
      }

      img {
        width: 100%;
        height: 30vh;
        object-fit: cover;
      }

      .hover-items {
        position: absolute;
        top: 70%;
        left: 50%;
        transform: translate(-50%, -50%);
        padding: 1rem 2rem;
        visibility: hidden;

        li {
          overflow: hidden;
          a {
            text-decoration: none;
            font-family: inherit;

            img {
              width: 100%;
              height: 100%;
            }

            &:not(:last-child) {
              margin-right: 2rem;
            }
          }
        }
      }

      &:hover {
        .hover-items {
          transition: all .5s ease-in-out .2s;
          visibility: visible;
          top: 50%;
        }
      }
    }

    h5 {
      font-size: 1.7rem;
      font-weight: 400;
      color: white;
      transition: all .3s ease-in-out;

      &:hover {
        color: #037FFF;
        cursor: pointer;
      }
    }
  }
}